.tc-wrapper {
  flex-direction: column;
  display: flex;
}

.tc-tooltip {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #182c3a1a;
  border-radius: 10px;
  padding: 10px;
  transition: transform .5s ease-out;
  position: absolute;
  top: 5px;
  box-shadow: -1px 1px 4px #0000001a;
}

.tc-tooltip--hidden {
  display: none;
}

.tc-tooltip__wrapper {
  flex-direction: column;
  min-width: 140px;
  margin-top: 8px;
  font-size: 12px;
  display: flex;
}

.tc-tooltip__value {
  justify-content: space-between;
  align-items: center;
  line-height: 2em;
  display: flex;
}

.tc-tooltip__value--hidden {
  display: none;
}

.tc-tooltip__value span, .tc-tooltip__name {
  font-weight: bold;
}

.tc-tooltip__arrow {
  border-bottom: 1px solid #d2d5d7;
  border-right: 1px solid #d2d5d7;
  width: .6em;
  height: .6em;
  transform: rotate(-45deg);
}

.tc-chart-wrapper {
  position: relative;
}

.tc-chart {
  width: 100%;
  height: 300px;
  margin-bottom: 30px;
}

.tc-preview {
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-bottom: 10px;
}

.tc-controls {
  flex-flow: wrap;
  align-items: center;
  display: flex;
}

.tc-control {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  border: 1px solid #e6ecf0;
  border-radius: 30px;
  align-items: center;
  margin: 5px;
  padding: 5px 15px 5px 5px;
  display: flex;
}

.tc-checkbox {
  border: 1px solid #e6ecf0;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin-right: 8px;
  position: relative;
}

.tc-checkbox:after, .tc-checkbox:before {
  content: "";
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 1px;
  width: 0;
  transition: transform .3s;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
}

.tc-checkbox:after {
  height: 12px;
  transform: translate(-1px, -6px)rotate(45deg);
}

.tc-checkbox:before {
  height: 12px;
  transform: translate(-1px, -6px)rotate(-45deg);
}

.tc-control.tc-checked > .tc-checkbox:after {
  height: 6px;
  top: 14px;
  left: 10px;
  transform: rotate(-45deg);
}

.tc-control.tc-checked > .tc-checkbox:before {
  top: 9px;
  left: 15px;
  transform: rotate(45deg);
}

.tc-range {
  text-align: right;
  justify-content: space-between;
  width: 150px;
  font-size: 12px;
  font-weight: bold;
  display: flex;
}

.tc-range div {
  width: 72px;
}

.tc-range div:first-child {
  margin-right: 3px;
}

.apply-shake {
  animation: .8s both wobble-hor-bottom;
}

@keyframes wobble-hor-bottom {
  0%, 100% {
    transform-origin: 50%;
    transform: translateX(0%);
  }

  15% {
    transform: translateX(-5px)rotate(-4deg);
  }

  30% {
    transform: translateX(3px)rotate(4deg);
  }

  45% {
    transform: translateX(-3px)rotate(-2deg);
  }

  60% {
    transform: translateX(1px)rotate(2deg);
  }

  75% {
    transform: translateX(-1px)rotate(-.6deg);
  }
}

